import React from "react";
import { IUser, IUserState } from "data/user";

interface IProps extends IUserState {
  searchTerm: string;
  setSearchResults: any;
  selected: IUser;
  setSelected: any;
  setNewMode: any;
}

export const List: React.FunctionComponent<IProps> = (props) => {
  const { users, selected, setSelected, setNewMode } = props;

  const selectActivity = (user: IUser) => {
    setSelected(user);
    setNewMode(false);
  };

  return (
    <div className="list">
      {/* ACTIVITY */}
      {users.map((user) => (
        <div key={user.id}>
          <div
            className={
              user.id === selected.id ? "list-item selected" : "list-item"
            }
            onClick={() => selectActivity(user)}
          >
            <div className="list-item-icon">
              <a>SE</a>
            </div>
            <div className="list-item-data">
              <div className="list-item-title">{user.name}</div>
              <div className="list-item-read-time">5 minutes read</div>
            </div>
          </div>
        </div>
      ))}
    </div>
  );
};

export default List;
