import React from "react";
import { IGoal, IGoalState } from "data/goal";

interface IProps extends IGoalState {
  searchTerm: string;
  setSearchResults: any;
  selected: IGoal;
  setSelected: any;
  setNewMode: any;
}

export const List: React.FunctionComponent<IProps> = (props) => {
  const { goals, selected, setSelected, setNewMode } = props;

  const selectActivity = (activity: IGoal) => {
    setSelected(activity);
    setNewMode(false);
  };

  return (
    <div className="list">
      {/* ACTIVITY */}
      {/* <ReactSortable className="sortable" list={activities} setList={setSearchResults}> */}
      {goals.map((goal) => (
        <div key={goal.id}>
          {/* <div>{CategoryTypes[activity.categoryId]}</div> */}
          <div
            className={
              goal.id === selected.id ? "list-item selected" : "list-item"
            }
            onClick={() => selectActivity(goal)}
          >
            <div
              className="list-item-icon"
              style={{ backgroundColor: goal.color }}
            >
              <a>SE</a>
            </div>
            <div className="list-item-data">
              <div className="list-item-title">{goal.title}</div>
              <div className="list-item-read-time">5 minutes read</div>
            </div>
          </div>
        </div>
      ))}
      {/* </ReactSortable> */}
    </div>
  );
};

export default List;
