import React from "react";
import { IActivity, IActivityState } from "data/activity";

interface IProps extends IActivityState {
  searchTerm: string;
  setSearchResults: any;
  selected: IActivity;
  setSelected: any;
  setNewMode: any;
}

export const List: React.FunctionComponent<IProps> = (props) => {
  const { activities, selected, setSelected, setNewMode } = props;

  const selectActivity = (activity: IActivity) => {
    setSelected(activity);
    setNewMode(false);
  };

  return (
    <div className="list">
      {/* ACTIVITY */}
      {/* <ReactSortable className="sortable" list={activities} setList={setSearchResults}> */}
      {activities.map((activity) => (
        <div key={activity.id}>
          {/* <div>{CategoryTypes[activity.categoryId]}</div> */}
          <div
            className={
              activity.id === selected.id ? "list-item selected" : "list-item"
            }
            onClick={() => selectActivity(activity)}
          >
            <div className="list-item-icon">
              <a>SE</a>
            </div>
            <div className="list-item-data">
              <div className="list-item-title">{activity.title}</div>
              <div className="list-item-read-time">5 minutes read</div>
            </div>
          </div>
        </div>
      ))}
      {/* </ReactSortable> */}
    </div>
  );
};

export default List;
