import React, { useState, useEffect } from "react";
import { IActivity } from "data/activity";
import { sideviewIcons } from "data/featherIcons";
import { useTypedDispatch } from "hooks/useTypedDispatch";

import { editActivity } from "store/activities/actions";
import { removeActivity } from "store/activities/actions";
import { useFormFields } from "hooks/useFormFields";

interface Iprops {
  selected: IActivity;
  setSelected: any;
  setDeleting: any;
}

export const SideView: React.FunctionComponent<Iprops> = (props) => {
  const { id, title, body, date, categoryId, userId } = props.selected;
  const { selected, setSelected, setDeleting } = props;

  const dispatch = useTypedDispatch();
  const [fields, handleFieldChange, setForm] = useFormFields(selected);
  const [editMode, setEditMode] = useState(false);
  const [editIcon, deleteIcon] = sideviewIcons;
  const [lastEdit, setLastEdit] = useState("Last edit at " + date);

  const tabs = ["Activity", "My Notes", "Goal"];
  const [tabMode, setTabMode] = useState(0);

  useEffect(() => {
    setEditMode(false);
    setForm(selected);
  }, [selected]);

  useEffect(() => {
    if (editMode) {
      let timer = setTimeout(() => {
        dispatch(editActivity(fields));
      }, 1000);
      return () => {
        clearTimeout(timer);
        setSelected(selected);
      };
    }
  }, [fields.title, fields.body]);

  const deleteItem = () => {
    setDeleting(true);
    dispatch(removeActivity(id));
  };

  return (
    <div>
      <div className="sideview-functions">
        <div className="item">{lastEdit}</div>
        <div
          className={editMode ? "item edit active" : "item edit"}
          onClick={() => setEditMode(!editMode)}
        >
          {editIcon}
        </div>
        <div className="item delete" onClick={deleteItem}>
          {deleteIcon}
        </div>
      </div>
      <div className="sideview-item">
        <div className="sideview-item-menus">
          {/* Show all tabs and set selected class  */}
          <div className="tabs">
            {tabs.map((tab, index) => (
              <div
                key={index}
                className={index === tabMode ? "tab selected" : "tab"}
              >
                <a onClick={() => setTabMode(index)}>{tab}</a>
              </div>
            ))}
          </div>
          <div className="action">
            <input type="submit" value="click to complete" />
          </div>
        </div>
        <div className="sideview-content">
          <div className="sideview-content-title">
            <input
              id="title"
              placeholder="Add title..."
              value={fields.title}
              onChange={handleFieldChange}
              disabled={!editMode}
            />
          </div>
          <div className="sideview-content-item">
            <textarea
              id="body"
              placeholder="Description..."
              value={fields.body}
              onChange={handleFieldChange}
              disabled={!editMode}
            ></textarea>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SideView;
